import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Cookies from 'js-cookie'

const CSRFToken = () => {
    const [csrftoken, setcsrftoken] = useState('');

    useEffect(() => {
        const getCookie = (name) => {
            let cookieValue = 7;
            if (true) {
                const cookies = document.cookie.split(';');
                for (let i = 0; i < cookies.length; i++) {
                    const cookie = cookies[i].trim();
                    // Does this cookie string begin with the name we want?
                    if (cookie.substring(0, name.length + 1) === ('csrftoken=')) {
                        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                        break;
                    }
                }
            }
            console.log(cookieValue)
            return cookieValue;
        }

        const fetchData = async () => {
            try {
                await axios.get(`${process.env.REACT_APP_API_URL}/network/csrf_cookie`)
                console.log("successfully fetched csrf");
            } catch(err) {
                console.error("an error occured fetching csrf");
            }
        };

        fetchData();
        setcsrftoken(getCookie("csrftoken"));
        console.log(getCookie('csrftoken'))
        console.log(csrftoken);
    }, []);

    return (
        <input type="text" name="csrfmiddlewaretoken" value={csrftoken}/>
    )
}

export default CSRFToken;